.icon-left {
  position: absolute;
  top: 10px;
  left: 10px;
}
.icon-right {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.season-display {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.winter {
  background: aliceblue;
}

.summer {
  background: lightgoldenrodyellow;
}

.coordinates {
  position: absolute;
  bottom: 10px;
  left: 10px;
}

.fraise {
  display: block;
}
